@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;500;700&family=Noto+Sans&family=Open+Sans:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu",
    "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

html,
body {
  background-color: #0a0a0a;
  font-family: 'Josefin Sans', sans-serif;
  color: white;
}

::selection {
  background-color: rgba(143, 143, 143, 0.712);
}

::-webkit-scrollbar {
  width: 12pt;
  height: 12pt;
}
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 6pt;
}
::-webkit-scrollbar-thumb {
  background: rgb(53, 53, 53);
  border-radius: 6pt;
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(63, 63, 63);
}

.SubwayPath-container {
  position: absolute;
  width: 15in;
}

.SubwayLines-container {
  position: fixed;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  -webkit-filter: blur(4px);
  filter: blur(4px);
}

.SocialsCard-container {
  padding: 2em;
  border-radius: 1em;
  display: flex;
  flex-direction: column;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  background-color: rgba(51, 51, 51, 0.749);
}

.links {
  display: flex;
  font-weight: lighter;
  flex-direction: column;
  grid-gap: 1.6em;
  gap: 1.6em;
}

h2 {
  margin-top: 0;
}

@media (max-width: 500px) {
  .SocialsCard-container {
    background: rgba(51, 51, 51, 0.5);
  }

  .links {
    align-items: center;
  }
}

.FancyLink {
  display: flex;
  align-items: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  cursor: pointer;
  text-decoration: none;
  grid-gap: 1em;
  gap: 1em;
  font-size: 1.3em;
}

.FancyLink-label {
  cursor: pointer;
  color: white;
  text-decoration: none;
  position: relative;
}

.FancyLink-label::before,
.FancyLink-label::after {
  position: absolute;
  width: 100%;
  height: 1px;
  background: currentColor;
  top: 100%;
  left: 0;
  pointer-events: none;
}

.FancyLink-label::before {
  content: '';
}

.FancyLink-label-underline::before {
  -webkit-transform-origin: 100% 50%;
          transform-origin: 100% 50%;
  -webkit-transform: scale3d(0, 1, 1);
          transform: scale3d(0, 1, 1);
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.FancyLink-label-underline:hover::before {
  -webkit-transform-origin: 0% 50%;
          transform-origin: 0% 50%;
  -webkit-transform: scale3d(1, 1, 1);
          transform: scale3d(1, 1, 1);
}

.FancyLink img {
  width: 1.5em;
  -webkit-filter: invert(85%);
  filter: invert(85%);
  transition: 0.2s ease-in-out;
}

.FancyLink:hover img {
  -webkit-filter: invert(100%);
  filter: invert(100%);
  -webkit-transform: scale(1.1, 1.1);
          transform: scale(1.1, 1.1);
}

@media (max-width: 500px) {
  .FancyLink-label {
    display: none;
  }
}

.FrontPage {
  text-align: center;
}

.FrontPage-title {
  display: flex;
  align-items: baseline;
  font-weight: lighter;
  grid-gap: 0.1em;
  gap: 0.1em;
  margin-bottom: 5vh;
  font-size: 50pt;
}

.FrontPage-title a {
  font-size: 0.15em;
  color: #e6e6e6;
  text-decoration: none;
}

.FrontPage-title a:hover {
  text-decoration: underline;
}

.FrontPage-components {
  position: relative;
  z-index: 999;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* Mobile Styling */
@media (max-width: 500px) {
  .FrontPage-title {
    flex-direction: column;
    align-items: center;
  }
}

